import React from 'react';

import { makeStyles } from '@material-ui/core';
import ReactMarkdown from 'react-markdown';

import CodeBlock from "../components/code-block";
import { CalendarIcon } from '../components/icons'
export default function Markdown(param) {

    const classes = useStyles();


    function Image(props) { return <img className={classes.image} {...props} alt={param.seoUrl} /> }
    function BlockQuote(props) { return <blockquote className={classes.blockquote} {...props} /> }
    function ThematicBreak(props) { return <div className={classes.thematicbreak} {...props} /> }
    function InlineCode(props) { return <code className={classes.inlinecode} {...props} /> }
    function Paragraph(props) { return <p className={classes.paragraph} {...props} /> }
    function Strong(props) { return <p className={classes.strong} {...props} /> }
    function Table(props) { return <table className={classes.table} {...props} /> }
    function TableCell(props) { return <tablecell className={classes.tablecell} {...props} /> }

    return (
        <>
            <CalendarIcon className={classes.calendarIcon} />
            <span className={classes.date}>{param.date}</span>
            <ReactMarkdown source={param.source} renderers={{ code: CodeBlock, image: Image, blockquote: BlockQuote, inlineCode: InlineCode, thematicBreak: ThematicBreak, paragraph: Paragraph, table: Table, tableCell: TableCell, strong: Strong }} />
        </>
    );
};

const useStyles = makeStyles({
    date: {
        color: '#6d6d6d',
        paddingLeft: 10,
        fontWeight: '500',
        verticalAlign: '13%'
    },
    calendarIcon: {
        width: 18,
        height: 19,
    },
    image: {
        width: '100%',
    },
    thematicbreak: {
        backgroundColor: '#ccc',
        height: 1,
        marginTop: 40,
        marginBottom: 40
    },
    tablecell: {
        display: 'table-cell',
        border: '1px solid  rgba(0,0,0,0.1);',
        padding: 15,
    },
    table: {
        overflowX: 'auto',
        display: 'block',
        borderSpacing: 0,
    },
    paragraph: {
        display: 'inline-block',
        opacity: 0.8,
        fontSize: 21,
        fontFamily: 'medium-content-serif-font,Cambria, "Times New Roman", Times, serif;',
        lineHeight: 1.58,
    },
    strong: {
        display: 'inline-block',
        fontWeight: '600',
        fontSize: 18,
        fontFamily: 'monospace',
    },
    blockquote: {
        margin: 0,
        width: 'fit-content',
        fontSize: 38,
        position: 'relative',
        justifyContent: 'center',
        display: 'flex',
        color: 'grey',
        fontStyle: 'italic',
        '&:before': {
            position: 'absolute',
            left: 0,
            top: -2,
            fontFamily: 'Sanchez',
            fontWeight: '800',
            color: 'black',
            content: 'open-quote',
        },
        '&:after': {
            position: 'absolute',
            bottom: -17,
            right: 0,
            fontFamily: 'Sanchez',
            fontWeight: '800',
            color: 'black',
            content: 'close-quote',
        }
    },
    inlinecode: {
        display: 'inline-block',
        fontSize: 18,
        paddingLeft: 5,
        paddingRight: 5,
        background: '#F3F4F4',
        borderRadius: 3,
    },
});