import React, { useEffect, useState } from 'react';


import { Helmet } from 'react-helmet'
import Markdown from "../components/markdown";
import data from '../data/data'
import NotFound from '../components/not-found'
import '../index.css'

export default function BlogDetailPage({ match }) {
  const seoUrl = match.params.seoUrl
  const index = data.findIndex(data => data.seoUrl === match.params.seoUrl);
  const content = (index === -1) ? null : require(`../data/markdown/${match.params.seoUrl}.md`)
  const title =(index !== -1)? data[index].title : '';
  const [md, setMd] = useState(null);
  const [desc, setDesc] = useState(null);

  function getDesc() {
    if(content){
      setDesc((('' + document.body.innerText.split(data[index].title)[1]).split('. ')[0]).trim())
    }
  }
  useEffect(() => {
    getDesc();
    document.title = (content) ? title : 'Sayfa Bulunamadı'
    return () => {
      document.title = 'My Blog'
      setDesc('My Blog')
    }
  });
  useEffect(() => {
    fetch(content)
      .then((response) => response.text())
      .then((text) => { setMd(text) })
  });
  if (content) {
    return (
      <>
        <Helmet>
          <meta name="description" content={desc} />
        </Helmet>
        <div style={{ display: 'flex', justifyContent: 'center' }}>
          <div className='media' style={{ marginTop: 10, padding: 10 }}>
            <Markdown seoUrl={seoUrl} date={data[index].date} source={md} />
          </div>
        </div>
      </>
    )
  } else {
    return (<NotFound />
    );
  }
};
